@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#menu{
    width: 100vw;
    height: 2.7rem;
    background-color: #EFEFEF;
    display: flex;
    flex-direction: column;
    z-index: 200;
    position: fixed;
    border-bottom: solid 1px black;
}

#icon-holder{
width: 100vw;
}

#b-menu{
    background-image: url(data:image/png;base64,);
    width: 1rem;
}

#d-menu-icon{
   width: 2.5rem;
}

#img-menu-icon{
  display: black;
  height: auto;
  width: 100%;
}

#items{
    display: flex;
    flex-direction: column;
    width: 100vw;
    text-align: center;
    background-color: #EFEFEF;
    padding-top: 1rem;
}

#menu-item{
    text-align: left;
    background-color: #EFEFEF;
    height: 2rem;
    margin-top: 0;
    padding-left: 1rem;
    font-family: 'Crimson Text', serif;
    font-size: 1.2rem;
}

#menu-item-last {
    border-bottom: solid 1px black;
    padding-bottom: 1rem;
    padding-left: 1rem;
    text-align: left;
    font-family: 'Crimson Text', serif;
    font-size: 1.2rem;
}
#d-landing{
    width: 100vw;
    font-size: 2rem;
    margin-top: 4rem;
}
#d-loading{
 font-size: 2.5rem;
 color: black;
 display: flex;
 flex-direction: column;
 background-color: #A9A9A9;
 width: 79vw;
 margin: 10rem 10vw 0 10vw;
 padding: 5rem 0 5rem 0;
 text-align: left;
 font-family: 'Crimson Text', serif;
 border-left: solid 0.7rem black;
}

#loading-name{
 padding-left: 1rem;
}

#loading-title{
 text-align: center;
 font-size: 1.5rem;
 text-align: left;
 padding-left: 1rem;
}
#main-container{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

body, html {
    background-color: #CAEBF2;
}
